.navbar {
  position: fixed;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
  width: auto;
  z-index: 1000;
  padding: 0;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.navbar.scrolled {
  right: 25px;
}

.nav-container {
  background: rgba(30, 41, 59, 0.7);
  backdrop-filter: blur(25px);
  border: 1px solid rgba(71, 85, 105, 0.3);
  border-radius: 50px;
  padding: 25px 18px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.05) inset;
  width: 80px;
  transform: translateY(-200px);
}


.nav-container::before {
  content: '';
  position: absolute;
  top: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: top 0.5s;
}

.nav-container:hover::before {
  top: 100%;
}

.nav-logo {
  display: none;
}

.nav-menu {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  position: relative;
  z-index: 2;
}

@media (max-width: 1024px) and (min-width: 769px) {
  .navbar {
    right: 20px;
  }

  .nav-container {
    padding: 20px 15px;
    width: 70px;
  }

  .nav-menu {
    gap: 18px;
  }

  .nav-link {
    width: 42px;
    height: 42px;
    padding: 10px;
  }
}

.nav-link {
  color: rgba(148, 163, 184, 0.8);
  text-decoration: none;
  font-weight: 400;
  font-size: 0;
  position: relative;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  padding: 12px;
  border-radius: 50%;
  text-align: center;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
}

.nav-link:hover {
  color: #ffffff;
  background: rgba(59, 130, 246, 0.8);
  transform: scale(1.1);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.3);
}

.nav-link.active {
  color: #ffffff;
  background: rgba(59, 130, 246, 0.9);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.4);
}

.nav-link::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 50%;
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.1), rgba(147, 51, 234, 0.1));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.nav-link:hover::before {
  opacity: 1;
}

.nav-link::after {
  display: none;
}

.nav-toggle {
  display: none;
  cursor: pointer;
  color: #ffffff;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  padding: 8px;
  border-radius: 50%;
  transition: all 0.3s ease;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
}

.nav-toggle:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.1);
}

.nav-toggle:active {
  transform: scale(0.95);
}

.mobile-menu {
  position: fixed;
  top: 80px;
  left: 50%;
  transform: translateX(-50%);
  width: 280px;
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  gap: 15px;
  z-index: 999;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.mobile-menu.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

/* Mobile menu backdrop */
.mobile-menu-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(2px);
  z-index: 998;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.mobile-menu-backdrop.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.mobile-link {
  color: rgba(148, 163, 184, 0.9);
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
  padding: 12px 20px;
  border-radius: 15px;
  text-align: center;
  display: flex;
  align-items: center;
  gap: 12px;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  min-height: 44px;
  /* iOS touch target minimum */
}

.mobile-link:hover {
  color: #ffffff;
  background: rgba(59, 130, 246, 0.2);
  transform: translateY(-2px);
}

.mobile-link:active {
  transform: translateY(0) scale(0.98);
  background: rgba(59, 130, 246, 0.3);
}

.mobile-link-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

@media (max-width: 768px) {
  .navbar {
    top: 20px;
    right: 20px;
    left: 20px;
    transform: none;
    width: auto;
  }

  .nav-menu {
    display: none !important;
  }

  .nav-toggle {
    display: block !important;
    background: rgba(59, 130, 246, 0.2);
    color: rgba(148, 163, 184, 0.9);
    padding: 10px;
    border-radius: 12px;
    border: 1px solid rgba(71, 85, 105, 0.3);
    z-index: 1001;
    position: relative;
  }

  .nav-toggle:hover {
    background: rgba(59, 130, 246, 0.3);
    color: #ffffff;
    transform: scale(1.05);
  }

  .nav-container {
    padding: 15px 20px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    border-radius: 20px;
    background: rgba(30, 41, 59, 0.85);
    backdrop-filter: blur(25px);
  }

  .nav-logo {
    display: block;
    font-size: 1.2rem;
    font-weight: 700;
    cursor: pointer;
    color: rgba(148, 163, 184, 0.9);
  }

  .mobile-menu {
    top: 85px !important;
    right: 20px !important;
    left: 20px !important;
    width: auto !important;
    transform: none !important;
    background: rgba(30, 41, 59, 0.95) !important;
    border: 1px solid rgba(71, 85, 105, 0.4) !important;
    border-radius: 16px !important;
    padding: 20px !important;
    backdrop-filter: blur(25px) !important;
  }

  .mobile-menu.active {
    opacity: 1 !important;
    visibility: visible !important;
    pointer-events: all !important;
  }

  .mobile-link {
    padding: 15px 20px;
    border-radius: 12px;
    font-size: 1rem;
    justify-content: flex-start;
    text-align: left;
  }

  .mobile-link:hover {
    background: rgba(59, 130, 246, 0.15);
    transform: translateX(5px);
  }
}

@media (max-width: 480px) {
  .navbar {
    top: 15px;
    right: 15px;
    left: 15px;
  }

  .nav-container {
    padding: 12px 16px;
    border-radius: 16px;
  }

  .nav-logo {
    font-size: 1.1rem;
  }

  .nav-toggle {
    padding: 8px;
  }

  .mobile-menu {
    top: 75px;
    right: 15px;
    left: 15px;
    padding: 16px;
    border-radius: 14px;
  }

  .mobile-link {
    padding: 12px 16px;
    font-size: 0.95rem;
  }

  .mobile-link-icon {
    width: 18px;
    height: 18px;
  }
}

/* Extra small devices (phones in portrait) */
@media (max-width: 375px) {
  .navbar {
    top: 10px;
    right: 10px;
    left: 10px;
  }

  .nav-container {
    padding: 10px 14px;
    border-radius: 14px;
  }

  .nav-logo {
    font-size: 1rem;
  }

  .nav-toggle {
    padding: 6px;
  }

  .mobile-menu {
    top: 65px;
    right: 10px;
    left: 10px;
    padding: 14px;
    border-radius: 12px;
  }

  .mobile-link {
    padding: 10px 14px;
    font-size: 0.9rem;
    gap: 10px;
  }
}

/* Landscape orientation for mobile */
@media (max-height: 500px) and (orientation: landscape) {
  .navbar {
    top: 10px;
    right: 20px;
    transform: translateY(0);
    width: auto;
  }

  .nav-container {
    flex-direction: row;
    padding: 8px 12px;
    width: auto;
    border-radius: 20px;
  }

  .nav-menu {
    display: flex;
    flex-direction: row;
    gap: 8px;
  }

  .nav-link {
    width: 36px;
    height: 36px;
    padding: 8px;
  }

  .nav-toggle {
    display: none;
  }

  .nav-logo {
    display: none;
  }
}