.contact {
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.05) 0%, rgba(118, 75, 162, 0.05) 100%);
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: start;
}

.contact-info h3 {
  font-size: 2rem;
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 20px;
}

.contact-info p {
  font-size: 1.1rem;
  color: #b0b0b0;
  line-height: 1.8;
  margin-bottom: 40px;
}

.contact-details {
  margin-bottom: 40px;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 20px 0;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.contact-item:hover {
  color: #667eea;
}

.contact-icon {
  color: #667eea;
  flex-shrink: 0;
}

.contact-title {
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 5px;
}

.contact-value {
  color: #b0b0b0;
}

.social-links h4 {
  color: #ffffff;
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.social-icons {
  display: flex;
  gap: 15px;
}

.social-link {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.social-link:hover {
  background: #667eea;
  border-color: #667eea;
  color: #ffffff;
}

.contact-form {
  background: rgba(255, 255, 255, 0.05);
  padding: 40px;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.form-group {
  margin-bottom: 25px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 15px 20px;
  color: #ffffff;
  font-size: 1rem;
  transition: all 0.3s ease;
  resize: vertical;
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: #b0b0b0;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #667eea;
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.submit-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-size: 1rem;
  padding: 15px 30px;
}

@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 50px;
  }
  
  .contact-info {
    text-align: center;
  }
  
  .contact-info h3 {
    font-size: 1.5rem;
  }
  
  .contact-info p {
    font-size: 1rem;
  }
  
  .contact-item {
    justify-content: center;
    text-align: left;
  }
  
  .social-icons {
    justify-content: center;
  }
  
  .contact-form {
    padding: 30px 20px;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 12px 15px;
  }
}