.animated-logo {
  position: fixed;
  top: 40px;
  left: 40px;
  z-index: 1000;
  pointer-events: none;
  user-select: none;
}

.logo-backdrop {
  position: absolute;
  top: -15px;
  left: -15px;
  right: -15px;
  bottom: -15px;
  background: rgba(30, 41, 59, 0.1);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(96, 165, 250, 0.1);
  transition: all 0.3s ease;
}

/* Enhanced backdrop with subtle animation */
.logo-backdrop::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(96, 165, 250, 0.05) 0%, rgba(30, 41, 59, 0.05) 100%);
  border-radius: 12px;
  opacity: 0;
  animation: backdropGlow 8s ease-in-out infinite;
}

@keyframes backdropGlow {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

.logo-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  z-index: 2;
  gap: -5px;
}

.logo-word {
  font-family: 'Inter', sans-serif;
  font-weight: 300;
  font-size: 1.4rem;
  color: rgba(255, 255, 255, 0.9);
  letter-spacing: 0.1em;
  text-transform: uppercase;
  position: relative;
  transform-origin: left center;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.logo-word.second-word {
  font-weight: 600;
  font-size: 1.6rem;
  background: linear-gradient(135deg, #60a5fa 0%, #ffffff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-left: 20px;
  letter-spacing: 0.05em;
  position: relative;
  overflow: hidden;
}

/* Enhanced animated text effects */
.logo-word::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.logo-word.second-word::before {
  background: linear-gradient(90deg, transparent, rgba(96, 165, 250, 0.3), transparent);
}

.animated-logo:hover .logo-word::before {
  left: 100%;
}

/* Breathing text effect */
.logo-word {
  animation: textBreathe 6s ease-in-out infinite;
}

.logo-word.second-word {
  animation: textBreathe 6s ease-in-out infinite 0.5s;
}

@keyframes textBreathe {
  0%, 100% {
    transform: scale(1);
    opacity: 0.9;
  }
  50% {
    transform: scale(1.02);
    opacity: 1;
  }
}

/* Character-by-character reveal effect */
.logo-word.second-word::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 100%;
  background: linear-gradient(90deg, transparent, #60a5fa, #ffffff);
  animation: textReveal 4s ease-in-out infinite;
  opacity: 0.1;
}

@keyframes textReveal {
  0%, 90%, 100% {
    width: 0;
    opacity: 0;
  }
  10%, 80% {
    width: 100%;
    opacity: 0.1;
  }
}

.logo-line {
  position: absolute;
  bottom: -8px;
  left: 0;
  height: 1px;
  width: 120px;
  background: linear-gradient(90deg, transparent 0%, #60a5fa 50%, transparent 100%);
  transform-origin: left;
  box-shadow: 0 0 4px rgba(96, 165, 250, 0.3);
}

/* Enhanced line with pulse effect */
.logo-line::after {
  content: '';
  position: absolute;
  top: -1px;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, transparent 0%, rgba(96, 165, 250, 0.6) 50%, transparent 100%);
  filter: blur(2px);
  animation: linePulse 4s ease-in-out infinite;
  opacity: 0;
}

@keyframes linePulse {
  0%, 90%, 100% {
    opacity: 0;
    transform: scaleX(0);
  }
  10%, 80% {
    opacity: 1;
    transform: scaleX(1);
  }
}

.logo-dots {
  position: absolute;
  top: 50%;
  right: -25px;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.dot {
  width: 3px;
  height: 3px;
  background: #60a5fa;
  border-radius: 50%;
  box-shadow: 0 0 6px rgba(96, 165, 250, 0.6);
  position: relative;
}

/* Enhanced dot with ripple effect */
.dot::after {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border: 1px solid rgba(96, 165, 250, 0.3);
  border-radius: 50%;
  animation: dotRipple 3s ease-out infinite;
  opacity: 0;
}

@keyframes dotRipple {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(3);
    opacity: 0;
  }
}

/* Text selection styling */
.logo-word::selection {
  background: rgba(96, 165, 250, 0.3);
  color: #ffffff;
}

.logo-word::-moz-selection {
  background: rgba(96, 165, 250, 0.3);
  color: #ffffff;
}

/* Subtle hover effect */
.animated-logo:hover .logo-backdrop {
  background: rgba(30, 41, 59, 0.15);
  border-color: rgba(96, 165, 250, 0.2);
}

/* Responsive design */
@media (max-width: 768px) {
  .animated-logo {
    top: 25px;
    left: 25px;
  }
  
  .logo-word {
    font-size: 1.1rem;
  }
  
  .logo-word.second-word {
    font-size: 1.3rem;
    margin-left: 15px;
  }
  
  .logo-line {
    width: 100px;
  }
  
  .logo-dots {
    right: -20px;
  }
}

@media (max-width: 480px) {
  .animated-logo {
    top: 20px;
    left: 20px;
  }
  
  .logo-word {
    font-size: 1rem;
  }
  
  .logo-word.second-word {
    font-size: 1.2rem;
    margin-left: 12px;
  }
  
  .logo-line {
    width: 80px;
  }
  
  .logo-dots {
    right: -15px;
  }
  
  .dot {
    width: 2px;
    height: 2px;
  }
}

/* Ensure proper layering */
@media (max-width: 768px) {
  .animated-logo {
    z-index: 999;
  }
}