.about {
  background: rgba(255, 255, 255, 0.02);
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: center;
}

.about-text h3 {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 30px;
  color: #ffffff;
}

.about-text p {
  font-size: 1.1rem;
  line-height: 1.8;
  color: #b0b0b0;
  margin-bottom: 25px;
}

.about-stats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.stat-item {
  background: rgba(255, 255, 255, 0.05);
  padding: 30px 20px;
  border-radius: 20px;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
}

.stat-item:hover {
  background: rgba(102, 126, 234, 0.1);
  border-color: rgba(102, 126, 234, 0.3);
  transform: translateY(-5px);
}

.stat-icon {
  color: #667eea;
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 10px;
}

.stat-label {
  font-size: 0.9rem;
  color: #b0b0b0;
  font-weight: 500;
}

@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 50px;
  }
  
  .about-text {
    text-align: center;
  }
  
  .about-text h3 {
    font-size: 1.5rem;
  }
  
  .about-text p {
    font-size: 1rem;
  }
  
  .about-stats {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  
  .stat-item {
    padding: 20px 15px;
  }
  
  .stat-number {
    font-size: 2rem;
  }
}